___CSS_LOADER_EXPORT___.push([module.id, `:host {
  --width: min(70vw, 480px);

  height: 100dvh;
  position: fixed;
  z-index: 3;
  top: 0;
  left: 0;
  right: 0;
  user-select: none;
}

.backdrop {
  position: relative;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.3);
  backdrop-filter: blur(2px);
}

.drawer {
  pointer-events: all;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
  height: 100%;
  width: var(--width);
  background-color: rgb(245, 245, 245);
  box-shadow: 2px 0 4px rgba(0, 0, 0, 0.15);

  transition-property: left;
  transition-duration: 0.25s;
  transition-timing-function: cubic-bezier(0.075, 0.82, 0.165, 1);

  & .header {
    box-sizing: border-box;

    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 4px 4px 4px 16px;

    & .title {
      font-weight: bold;
      font-size: 16px;
      color: rgb(105, 105, 105);
    }
  }

  & .content {
    display: flex;
    flex-direction: column;
    flex: 1;
  }

  & .sub-title {
    background-color: rgb(215, 215, 215);
    font-weight: bold;
    text-transform: uppercase;
    font-size: 14px;
    color: rgb(95, 95, 95);
    padding: 8px;
  }

  & .option {
    all: unset;
    box-sizing: border-box;
    display: flex;
    width: 100%;
    height: 48px;
    align-items: center;
    justify-content: start;
    padding: 0 16px;
    font-size: 18px;
    font-weight: bold;
    color: rgb(85, 85, 85);

    gap: 8px;

    & sudoku-icon {
      --icon-color: rgba(35, 35, 35, 0.7);
    }

    &:hover {
      color: rgb(55, 55, 55);

      & sudoku-icon {
        --icon-color: rgba(35, 35, 35, 0.8);
      }
    }

    &:hover {
      background-color: rgba(0, 0, 0, 0.15);
    }
  }
}


.difficulty {
  text-transform: capitalize;

}

@starting-style {
  .drawer {
    left: calc(-1 * var(--width));
    pointer-events: none;
  }
}`, "",{"version":3,"sources":["webpack://./src/elements/sudoku-drawer/sudoku-drawer.css"],"names":[],"mappings":"AAAA;EACE,yBAAyB;;EAEzB,cAAc;EACd,eAAe;EACf,UAAU;EACV,MAAM;EACN,OAAO;EACP,QAAQ;EACR,iBAAiB;AACnB;;AAEA;EACE,kBAAkB;EAClB,WAAW;EACX,YAAY;EACZ,oCAAoC;EACpC,0BAA0B;AAC5B;;AAEA;EACE,mBAAmB;EACnB,kBAAkB;EAClB,MAAM;EACN,OAAO;EACP,SAAS;EACT,aAAa;EACb,sBAAsB;EACtB,sBAAsB;EACtB,YAAY;EACZ,mBAAmB;EACnB,oCAAoC;EACpC,yCAAyC;;EAEzC,yBAAyB;EACzB,0BAA0B;EAC1B,+DAA+D;;EAE/D;IACE,sBAAsB;;IAEtB,aAAa;IACb,8BAA8B;IAC9B,mBAAmB;IACnB,yBAAyB;;IAEzB;MACE,iBAAiB;MACjB,eAAe;MACf,yBAAyB;IAC3B;EACF;;EAEA;IACE,aAAa;IACb,sBAAsB;IACtB,OAAO;EACT;;EAEA;IACE,oCAAoC;IACpC,iBAAiB;IACjB,yBAAyB;IACzB,eAAe;IACf,sBAAsB;IACtB,YAAY;EACd;;EAEA;IACE,UAAU;IACV,sBAAsB;IACtB,aAAa;IACb,WAAW;IACX,YAAY;IACZ,mBAAmB;IACnB,sBAAsB;IACtB,eAAe;IACf,eAAe;IACf,iBAAiB;IACjB,sBAAsB;;IAEtB,QAAQ;;IAER;MACE,mCAAmC;IACrC;;IAEA;MACE,sBAAsB;;MAEtB;QACE,mCAAmC;MACrC;IACF;;IAEA;MACE,qCAAqC;IACvC;EACF;AACF;;;AAGA;EACE,0BAA0B;;AAE5B;;AAEA;EACE;IACE,6BAA6B;IAC7B,oBAAoB;EACtB;AACF","sourcesContent":[":host {\n  --width: min(70vw, 480px);\n\n  height: 100dvh;\n  position: fixed;\n  z-index: 3;\n  top: 0;\n  left: 0;\n  right: 0;\n  user-select: none;\n}\n\n.backdrop {\n  position: relative;\n  width: 100%;\n  height: 100%;\n  background-color: rgba(0, 0, 0, 0.3);\n  backdrop-filter: blur(2px);\n}\n\n.drawer {\n  pointer-events: all;\n  position: absolute;\n  top: 0;\n  left: 0;\n  bottom: 0;\n  display: flex;\n  flex-direction: column;\n  box-sizing: border-box;\n  height: 100%;\n  width: var(--width);\n  background-color: rgb(245, 245, 245);\n  box-shadow: 2px 0 4px rgba(0, 0, 0, 0.15);\n\n  transition-property: left;\n  transition-duration: 0.25s;\n  transition-timing-function: cubic-bezier(0.075, 0.82, 0.165, 1);\n\n  & .header {\n    box-sizing: border-box;\n\n    display: flex;\n    justify-content: space-between;\n    align-items: center;\n    padding: 4px 4px 4px 16px;\n\n    & .title {\n      font-weight: bold;\n      