  --gap: 12px;

  box-sizing: border-box;
  container-type: size;
  display: flex;
  height: 100%;
  width: 100%;
  flex-direction: column;

  align-items: center;
  padding: var(--gap);
  gap: var(--gap);
}

sudoku-board {
  /* 46 is the height of the input element */
  width: min(100cqw, 66cqh);
}

.input {
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  flex-direction: column;
  padding: 8px;
  gap: 8px;
  width: 100%;

  border-radius: 4px;
  background-color: rgb(255, 255, 255);
  box-shadow: 0px 1px 5px 0px rgba(0, 0, 0, 0.2);
}

textarea {
  resize: none;
  box-sizing: border-box;
  width: 100%;
  font-size: 24px;
  font-family: "Roboto", Verdana, sans-serif;
  border-radius: 2px;
