___CSS_LOADER_EXPORT___.push([module.id, `:host {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;

  box-sizing: border-box;
  background-color: rgb(255, 255, 255);
  box-shadow: 0px 1px 5px 0px rgba(0, 0, 0, 0.2);
  border-radius: 4px;
  padding-top: 6px;
}

.buttons {
  display: flex;
  align-items: center;
  container-type: size;
  justify-content: center;
  width: 100%;
  height: 32px;

  box-sizing: border-box;
  margin-left: 6px;
  margin-right: 6px;
  gap: 4px;
}

button {
  all: unset;
  display: flex;
  align-items: center;
  justify-content: center;
  width: min(100cqh, 9cqw);
  aspect-ratio: 1/1;
  border-radius: 16px;
  background-color: rgb(70, 70, 70);
  color: rgb(255, 255, 255);
  font-size: min(50cqh, 2.5rem);
  user-select: none;
}

button:is(:hover, :focus) {
  background-color: rgb(90, 90, 90);
}

button:active {
  background-color: rgb(110, 110, 110);
  box-shadow: 0px 1px 4px 1px rgba(0, 0, 0, 0.25);
}

.progress {
  margin-top: 4px;
  height: 4px;
  width: 100%;
  border-bottom-right-radius: 4px;
  border-bottom-left-radius: 4px;
  background-color: rgb(190, 190, 190);
  overflow: hidden;
}

.indicator {
  width: 0%;
  height: 4px;
  background-color: rgb(80, 85, 80);

  &.complete {
    background-color: rgb(95, 155, 95);
  }
}`, "",{"version":3,"sources":["webpack://./src/elements/sudoku-input/sudoku-input.css"],"names":[],"mappings":"AAAA;EACE,aAAa;EACb,mBAAmB;EACnB,uBAAuB;EACvB,sBAAsB;;EAEtB,sBAAsB;EACtB,oCAAoC;EACpC,8CAA8C;EAC9C,kBAAkB;EAClB,gBAAgB;AAClB;;AAEA;EACE,aAAa;EACb,mBAAmB;EACnB,oBAAoB;EACpB,uBAAuB;EACvB,WAAW;EACX,YAAY;;EAEZ,sBAAsB;EACtB,gBAAgB;EAChB,iBAAiB;EACjB,QAAQ;AACV;;AAEA;EACE,UAAU;EACV,aAAa;EACb,mBAAmB;EACnB,uBAAuB;EACvB,wBAAwB;EACxB,iBAAiB;EACjB,mBAAmB;EACnB,iCAAiC;EACjC,yBAAyB;EACzB,6BAA6B;EAC7B,iBAAiB;AACnB;;AAEA;EACE,iCAAiC;AACnC;;AAEA;EACE,oCAAoC;EACpC,+CAA+C;AACjD;;AAEA;EACE,eAAe;EACf,WAAW;EACX,WAAW;EACX,+BAA+B;EAC/B,8BAA8B;EAC9B,oCAAoC;EACpC,gBAAgB;AAClB;;AAEA;EACE,SAAS;EACT,WAAW;EACX,iCAAiC;;EAEjC;IACE,kCAAkC;EACpC;AACF","sourcesContent":[":host {\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  flex-direction: column;\n\n  box-sizing: border-box;\n  background-color: rgb(255, 255, 255);\n  box-shadow: 0px 1px 5px 0px rgba(0, 0, 0, 0.2);\n  border-radius: 4px;\n  padding-top: 6px;\n}\n\n.buttons {\n  display: flex;\n  align-items: center;\n  container-type: size;\n  justify-content: center;\n  width: 100%;\n  height: 32px;\n\n  box-sizing: border-box;\n  margin-left: 6px;\n  margin-right: 6px;\n  gap: 4px;\n}\n\nbutton {\n  all: unset;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  width: min(100cqh, 9cqw);\n  aspect-ratio: 1/1;\n  border-radius: 16px;\n  background-color: rgb(70, 70, 70);\n  color: rgb(255, 255, 255);\n  font-size: min(50cqh, 2.5rem);\n  user-select: none;\n}\n\nbutton:is(:hover, :focus) {\n  background-color: rgb(90, 90, 90);\n}\n\nbutton:active {\n  background-color: rgb(110, 110, 110);\n  box-shadow: 0px 1px 4px 1px rgba(0, 0, 0, 0.25);\n}\n\n.progress {\n  margin-top: 4px;\n  height: 4px;\n  width: 100%;\n  border-bottom-right-radius: 4px;\n  border-bottom-left-radius: 4px;\n  background-color: rgb(190, 190, 190);\n  overflow: hidden;\n}\n\n.indicator {\n  width: 0%;\n  height: 4px;\n  ba